@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;400&display=swap);
*{
  box-sizing : border-box;
  padding: 0;
  margin:0;
}
a{
  text-decoration: none;
}
body {
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color:white;
  max-width: 1100px;
  margin: 0 auto;
  
}

a{
  color:wheat
}


.App{padding:20px;text-align:left}.won-card{color:#27AE60}.lost-card{color:#e15454}

.MatchDetailCard{border-style:dashed;display:grid;grid-template-columns:1fr 1fr;padding:20px;background-color:#17202A;margin-top:15px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.additonal-detail{text-align:right;color:gold}.additonal-detail>p{color:white;margin-bottom:10px}

.MatchSmallCard{padding:40px;border-style:dashed;align-self:center;background-color:#17202A}.MatchSmallCard .match-result{font-size:15px;margin-top:10px}.MatchSmallCard .small-team-name{font-size:15px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:auto auto 1fr;grid-column-gap:10px;grid-row-gap:10px}.team-name-section{grid-column:span 3;justify-self:start;align-self:end}.win-loss-section{justify-content:center;padding:40px;text-align:center}.team-name{font-size:4rem;margin-bottom:20px}.match-detail-section{grid-column:span 4}.more-link{align-self:center;justify-self:center;font-size:1.5rem}

.yearSelector{text-align:right;list-style-type:none}.yearSelector li{margin-top:10px;margin-bottom:10px}

.MatchPage{display:grid;grid-template-rows:1fr;grid-template-columns:1fr 4fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto}

.HomePage .app-name{font-size:3rem;margin-top:30px;text-align:center}.HomePage .team-grid{margin-top:50px;display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:15px}

.TeamTile{height:80px;text-align:center;padding:20px}.team-title{font-size:1.4rem}img{width:55px;height:45px}

